/**
    Font for personal use. 
 */
@font-face {
    font-family: neon-script;
    src: url(./assets/neon-script.ttf)
}

@font-face {
    font-family: andes;
    src: url(./assets/andes.ttf)
}

@font-face {
    font-family: market-deco;
    src: url(./assets/market-deco.ttf)
}

body {
    overflow: hidden;
}
body::-webkit-scrollbar {
    display: none;
}
.pf-body {
    width: 100vw;
    height: 200vh;
    background: linear-gradient(180deg, rgba(0,0,0,1) 0%, #136087 100%);
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
}

.pf-top-section {
    width: 100vw;
    height: 100vh;
}

.lighthouse-rflc {
    width: 20%;
    height: 20%;
    position: absolute;
    left: 40%;
    top: 77%;
    background: var(--rapture-fluorescent);
    box-shadow: 0px 0px 100px 10px var(--rapture-fluorescent);
    border-radius: 50%;
    filter: blur(35px);
    transform: rotateX(45deg);
}