@keyframes fade-in {
    from {
        opacity: 0;
    }
    to {
        opacity: 1;
    }
}
@keyframes fade-out {
    from {
        right: 0;
    }
    to {
        right: 10%;
    }
}

.pf-link {
    position: absolute;
    top: 0;
    display: flex;
    flex-direction: column;
    z-index: 3;
    right: 0;
    animation: fade-in 1s ease; 
}
.pf-link.hide-icons { animation: fade-out 1s ease; }
.icon-con {
    position: relative;
    margin-right: 20px;
    top: 20px;
    overflow: hidden;
    opacity: 0.4;
}
.link-icon { margin: 10px; }
.linkedin-container {
    width: 3em;
    height: 3em;
    position: relative;
    border-radius: 10%;
    cursor: pointer;
}
.linkedin-border {
    border: 5px solid white;
    position: absolute;
    width: calc(100% - 20px);
    height: calc(100% - 20px);
    top: 5px;
    left: 5px;
    border-radius: 5px;
}
.linkedin-i {
    position: absolute;
    height: 45%;
    width: 15%;
    background: #fff;
    bottom: 15%;
    left: 15%;
}
.linkedin-i::after {
    height: 40%;
    width: 120%;
    background: #fff;
    content: '';
    position: absolute;
    border-radius: 50%;
    bottom: 115%;
    left: -10%;
}
.linkedin-n {
    position: absolute;
    height: 45%;
    width: 15%;
    background: #fff;
    bottom: 15%;
    left: 40%;
    z-index: 3;

}
.linkedin-n::before {
    content: "";
    position: absolute;
    background: #fff;
    border-radius: 5em 5em 0 0;
    height: 100%;
    width: 224%;
    bottom: 0;
    left: 76.7%;
    z-index: 1;
}
.linkedin-n::after {
    content: "";
    position: absolute;
    background: black;
    border-radius: 5em 5em 0 0;
    height: 77.7%;
    width: 110%;
    bottom: 0;
    left: 100%;
    z-index: 1;
}

