.phone-container {
    width: 3em;
    height: 3em;
    position: relative;
    border-radius: 10%;
    cursor: pointer;
}
.phone-border {
    width: calc(100% - 20px);
    height: calc(100% - 20px);
    border: 5px solid white;
    position: absolute;
    border-radius: 50%;
    left: 5px;
    top: 5px;
}
.phone-border::before {
	content: '';
    bottom: -40%;
    left: -40%;
    border-width: 15px;
    border-style: solid;
    border-color: transparent #fff transparent transparent;
    position: absolute;
    transform: rotate(-45deg) rotateX(60deg);
}
.phone-border::after {
	content: '';
    bottom: calc(-40% + 5px);
    left: calc(-40% + 5px);
    border-width: 15px;
    border-style: solid;
    border-color: transparent black transparent transparent;
    position: absolute;
    transform: rotate(-45deg) rotateX(60deg);
}
.phone-holder {
    width: 70%;
    height: 25%;
    transform: rotate(45deg);
    background: white;
    left: 7%;
    position: absolute;
    top: 45%;
    border-radius: 0% 0% 45% 45% / 0% 0% 100% 100%;
}
.phone-holder-none {
    width: 40%;
    height: 35%;
    transform: rotate(45deg);
    background: black;
    left: 34%;
    position: absolute;
    top: 28%;
    border-radius: 0% 0% 15% 15% / 0% 0% 100% 100%;
}
.phone-ear {
    width: 20%;
    height: 17%;
    background: white;
    left: 20%;
    position: absolute;
    top: 25%;
    border-radius: 35% 15% 0% 60%;
    transform: rotate(45deg) skewY(15deg);
}
.phone-mouth {
    width: 20%;
    height: 17%;
    background: white;
    left: 56.5%;
    position: absolute;
    top: 59.5%;
    border-radius: 15% 35% 60% 0%;
    transform: rotate(45deg) skewY(-15deg);
}

.email-border {
    width: calc(100% - 20px);
    height: calc(100% - 20px);
    border: 5px solid white;
    position: absolute;
    border-radius: 10%;
    left: 5px;
    top: 5px;
}
.email-container {
    width: 3em;
    height: 3em;
    position: relative;
    border-radius: 10%;
    cursor: pointer;
}
.email-shape {
    left: 7.5px;
    top: calc(7.5% + 7.5px);
    background-color: #fff;
    position: absolute;
    height: calc(85% - 15px);
    width: calc(100% - 15px);
    overflow: hidden;
    border-radius: 10%;
}
.email-shape:before, .email-shape:after {
    content: '';
    position: absolute;
    transform: rotate(45deg) skew(345deg, 345deg);
    border-radius: 10%;
    width: 110%;
    background: #fff;
    padding-bottom: 110%;
}
.email-shape:before {
    bottom: -90%;
    left: -2px;
    box-shadow: -2px -2px 0 0 black;
}
.email-shape:after {
    top: -85%;
    left: -2px;
    box-shadow: 2px 2px 0 0 black;
}