:root {
  --neon-border-link-1: #3bb5f2;
  --neon-lg-text-shadow: 0 -40px 100px #fff, 0 0 2px, 0 0 1em #3bb5f2, 0 0 0.5em #3bb5f2, 0 0 0.1em #3bb5f2, 0 10px 3px #000;  
  /* 1px 0px 4px #FFD5FF, 2px 0px 4px #FFD5FF, 3px 0px 4px #FFD5FF, 2px 0px 3px #D42CCA, 2px 3px 15px #D42CCA, 2px 0px 15px, 5px 0px 25px, 10px 0vw 100px #D42CCA, 20px 0vw 100px #D42CCA; */
}

.neon-lg  {
  color: #e1f5ff;
  text-shadow: var(--neon-lg-text-shadow);
}
.b-neon { display: flex; justify-content: center; }
.neon-ltr.first-time-load { animation: blink linear 1.5s; cursor: pointer; }
.neon-ltr.first-time-load:nth-of-type(2) { animation: blink linear 4s; }
.neon-ltr.first-time-load:nth-of-type(3) { animation: blink linear 3s; }
.neon-ltr.first-time-load:nth-of-type(4) { animation: blink linear 2s; }
.neon-ltr.first-time-load:nth-of-type(5) { animation: blink linear 5s; }
.neon-ltr.first-time-load:nth-of-type(6) { animation: blink linear 3s; }
.neon-str { cursor: pointer; }
.neon-lg-rflc {
  display: block;
  transform: rotate3d(1, 0, 0, 180deg) scale(1, 1) skew(0deg, 0deg) perspective(15vh) rotateX(335deg);
  filter: blur(4px);
  margin-top: -2em;
}

@keyframes blink {
  78%, 81%, 83%, 92.5% {
    color: inherit;
    text-shadow: inherit;
  }
  79%{
     color: #333;
  }
  80% {
    text-shadow: none;
  }
  82%, 92% {
    color: #333;
    text-shadow: none;
  }
}
