.rain-container { position: relative; z-index: 2; height: 30vh; }
.raindrop {
    animation-iteration-count: infinite;
    animation-name: drop;
    animation-timing-function: linear;
    height: 10px;
    position: absolute;
    background: #a1c6cc;
    border: 1px solid #a1c6cc;
}
.wave {
    position: absolute;
    opacity: 0;
    border: #FFF 2px solid;
    border-radius: 50%;
    animation: ripple 2s infinite;
    width: 30px;
    height: 30px;
    filter: blur(2px);
    transform: perspective(15vh) rotateX(285deg);
}

@keyframes drop {
    90% {
        opacity: 1;
    }
    100% {
        opacity: 0;
        transform: translateY(85vh);
    }
}
@keyframes ripple {
    from {
        opacity: 0.7;
        margin-left: -15px;
        margin-top: -15px;
    }
    to {
        width: 60px;
        margin-left: -30px;
        margin-top: -30px;
        height: 60px;
        border-width: 1px;
        opacity: 0;
    }
}
